export default {
    created() {
        //没token  跳登录去
        uni.getStorage({
            key: 'token',
            success: function (res) {
                const token =res.data
                if(!token){
                    // 没有token
                    // 滚去login
                    uni.navigateTo({
                        url:"../login/login"
                    })
                    return;
                };
                if(token){
                    //查询是否存在
                }
            }
        });
        this.getData()
    },
    data() {
        return {
            URL:getApp().globalData.URL,
            tasklist:[],
            dytasklist:[],
            swiperlist: [],
            
        }
    },
    methods: {
        to(page) {
            uni.navigateTo({
                        url: `../${page}/${page}`
                    })
        },
        async getData() {
            // 任务列表
            await this.$u.get('/get_task_list').then(
                res => {
                    if (res.errorCode !== 0) return;
                      this.tasklist=res.data
                }
            )
            // 抖音任务列表
            await this.$u.get('/get_dy_task_list').then(
                res => {
                    console.log(res)
                    if (res.errorCode !== 0) return;
                      this.dytasklist=res.data
                }
            )
            
            // 获取轮播图
            await this.$u.get('/get_home_pic').then(
            res=>{
                if (res.errorCode !== 0) return;
                  this.swiperlist=res.data
            })
        },
        dy() {
            uni.navigateTo({
                url: "../douyinDetail/douyinDetail"
            })

        },
        openTaskDetail(task_id) {
            this.$u.route('pages/taskDetail/taskDetail', {
            	task_id
            });
        },
      
        openDyTaskDetail(dy_task_id) {
            this.$u.route('pages/douyinDetail/douyinDetail', {
            	dy_task_id
            });
        }
    },
}
